import React, { useState } from 'react';
import './App.css';
import { Tooltip } from 'react-tooltip'
import {Ilink} from "./types/Ilink"

function Link(links: Ilink) {
  return (
    <div className="InsideDock">
    {links.array.map((link) => (
        <a href={link.link}
        target="_blank"
        rel="noopener noreferrer">
        <button className='Link'
        data-tooltip-id="my-tooltip"
        data-tooltip-content={link.name}>
        <img src={link.image}
        alt={link.name} className="image"/>
        </button>
        </a>
    ))}
    <Tooltip id="my-tooltip" place="bottom" offset={10}/>
    </div>
  );
}

export default Link;
